.kiosk-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.menu-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.menu-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  margin: 20px;
  padding: 20px;
  border: 2px solid #ccc;
  border-radius: 10px;
  transition: all 0.3s ease;
  background-color: #f5f5f5;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.menu-card img {
  width: 150px;
  height: 150px;
  margin-bottom: 10px;
}

.menu-card p {
  margin: 0;
  font-size: 18px;
}

.menu-card.selected {
  background-color: #e0e0e0;
}

.order-section {
  margin-left: 20px;
}

.selected-menu {
  text-align: center;
}

.order-summary-card {
  background-color: #f5f5f5;
  padding: 20px;
  border-radius: 10px;
  margin-top: 20px;
}

.order-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.order-button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.order-button:hover {
  background-color: #0056b3;
}

.total-price {
  font-size: 20px;
  margin-top: 20px;
}
